<template>
  <v-app id="login" class="indigo">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm3 md6 lg6>
            <v-card class="elevation-1 pa-3">
              <v-card-text>
                <v-stepper v-model="stepper" vertical>
                    <v-stepper-step :complete="stepper > 1" step="1" @click="stepper = 1">
                    Data Pribadi
                    </v-stepper-step>

                    <v-stepper-content step="1">
                     <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12 sm6>
                            <v-text-field
                                prepend-inner-icon="perm_contact_calendar"
                                required
                                label="NIK"
                                v-model="editData.nik"
                            ></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                            <v-text-field
                                prepend-inner-icon="perm_contact_calendar"
                                required
                                label="Tempat Lahir"
                                v-model="editData.placeOfBirth"
                            ></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                            <v-text-field
                                prepend-inner-icon="perm_contact_calendar"
                                required
                                label="Nama lengkap"
                                v-model="editData.name"
                            ></v-text-field>
                            </v-flex>
                        </v-layout>
                     </v-container>
                    <v-btn color="primary" flat @click="stepper = 2">Continue</v-btn>
                    </v-stepper-content>

                    <v-stepper-step :complete="stepper > 2" step="2" @click="stepper = 2">Tanggal Lahir</v-stepper-step>
                    <v-stepper-content step="2">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12 sm4>
                            <v-select
                                :items="listDate"
                                v-model="date"
                                label="Tanggal"
                            ></v-select>
                            </v-flex>
                            <v-flex xs12 sm4>
                            <v-select
                                :items="listMonth"
                                item-value="number"
                                item-text="text"
                                v-model="month"
                                label="Bulan"
                            ></v-select>
                            </v-flex>
                            <v-flex xs12 sm4>
                             <v-select
                                :items="listYear"
                                v-model="year"
                                label="Tahun"
                            ></v-select>
                            </v-flex>
                        </v-layout>
                     </v-container>
                    <v-btn color="primary" @click="stepper = 3" flat>Continue</v-btn>
                    </v-stepper-content>
                    <v-stepper-step :complete="stepper > 3" step="3" @click="stepper = 3">Alamat Lengkap</v-stepper-step>
                    <v-stepper-content step="3">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>
                            <v-text-field
                                prepend-inner-icon="place"
                                required
                                label="Alamat"
                                v-model="editData.address"
                            ></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm4>
                            <v-text-field
                                prepend-inner-icon="perm_contact_calendar"
                                required
                                label="RT/RW"
                                v-model="editData.association"
                            ></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm4>
                            <v-text-field
                                prepend-inner-icon="perm_contact_calendar"
                                required
                                label="Kel/Desa"
                                v-model="editData.village"
                            ></v-text-field>
                            </v-flex>
                             <v-flex xs12 sm4>
                            <v-text-field
                                prepend-inner-icon="perm_contact_calendar"
                                required
                                label="Kecamatan"
                                v-model="editData.subdistrict"
                            ></v-text-field>
                            </v-flex>
                        </v-layout>
                     </v-container>
                    <v-btn color="primary" @click="stepper = 4" flat>Continue</v-btn>
                    </v-stepper-content>

                    <v-stepper-step :complete="stepper > 4" step="4" @click="stepper = 4">Kewarga Negaraan</v-stepper-step>

                    <v-stepper-content step="4">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs6>
                            <v-text-field
                                prepend-inner-icon="place"
                                required
                                label="Agama"
                                v-model="editData.religion"
                            ></v-text-field>
                            </v-flex>
                            <v-flex xs6>
                            <v-autocomplete
                              prepend-inner-icon="nature_people"
                              :items="['Belum Kawin','Sudah Kawin']"
                              label="Status Kawin"
                              v-model="editData.maritalStatus"
                            ></v-autocomplete>
                            </v-flex>
                             <v-flex xs6>
                              <v-text-field
                                    prepend-inner-icon="bookmarks"
                                    required
                                    label="Pekerjaan"
                                    v-model="editData.job"
                                  ></v-text-field>
                              </v-flex>
                              <v-flex xs6>
                                <v-autocomplete
                                  prepend-inner-icon="landscape"
                                  :items="['Wni','Wna']"
                                  label="Kependudukan"
                                  v-model="editData.citizenship"
                                ></v-autocomplete>
                              </v-flex>
                                <v-flex xs6>
                                <v-autocomplete
                                  prepend-inner-icon="landscape"
                                  :items="['Laki-laki','Perempuan']"
                                  label="Jenis Kelamin"
                                  v-model="editData.gender"
                                ></v-autocomplete>
                              </v-flex>
                                <v-flex xs6>
                                <v-autocomplete
                                  prepend-inner-icon="landscape"
                                  :items="['A','B','O','AB']"
                                  label="Golongan Darah"
                                  v-model="editData.bloodType"
                                ></v-autocomplete>
                              </v-flex>
                        </v-layout>
                     </v-container>
                    <v-btn color="primary" @click="stepper = 5" flat>Selesai</v-btn>
                    </v-stepper-content>
                </v-stepper>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                  <v-flex xs12>
                  <v-btn color="primary" v-if="typeInput == 'new'" dark @click.prevent="sendData()" :loading="load">Submit</v-btn>
                  </v-flex>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      stepper: 1,
      date: 1,
      month: 1,
      year: 1998,
      listMonth: [
        {number: 1, text: 'Januari'},
        {number: 2, text: 'Februari'},
        {number: 3, text: 'Maret'},
        {number: 4, text: 'April'},
        {number: 5, text: 'Mei'},
        {number: 6, text: 'Juni'},
        {number: 7, text: 'July'},
        {number: 8, text: 'Agustus'},
        {number: 9, text: 'September'},
        {number: 10, text: 'Oktober'},
        {number: 11, text: 'November'},
        {number: 12, text: 'Desember'}
      ],
      typeInput: 'new',
      role: '',
      stat: '',
      search: '',
      table: 0,
      users: [],
      dialog: false,
      load: false,
      deleteDialog: false,
      editDialog: false,
      snackbar: false,
      text: '',
      color: null,
      reset: false,
      editData: {},
      deleteId: -1,
      loading: false,
      loadData: true,
      isPaginated: true,
      isPaginationSimple: true,
      defaultSortDirection: 'desc',
      currentPage: 1,
      perPage: 10,
      roles: [],
      patiens: []
    }
  },
  computed: {
    listYear () {
      var year = []
      var today = new Date()
      var thisYear = today.getFullYear()
      var limitAge = 5
      var startYear = thisYear - limitAge
      for (let i = 0; i < 20; i++) {
        year.push(startYear - i)
      }
      return year
    },
    listDate () {
      var date = []
      for (let i = 1; i < 32; i++) {
        date.push(i)
      }
      return date
    }
  },
  methods: {
    sendData () {
      this.load = true
      var uri
      uri = this.$apiUrl + 'selfpatien'
      var lastDay = new Date(this.year, this.month + 1, 0)
      if (this.date > lastDay) {
        this.date = lastDay
      }
      this.editData.dateOfBirth = this.year + '-' + this.month + '-' + this.date
      this.$http.post(uri, this.editData).then(response => {
        console.log(response)
        this.$router.push({ name: 'scanQr', params: {qrcode: response.data} })
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    seteditData (data) {
      this.editData = {
        id: data.id,
        name: data.name,
        nik: data.detail.nik,
        placeOfBirth: data.detail.placeOfBirth,
        dateOfBirth: data.detail.dateOfBirth,
        gender: data.detail.gender,
        bloodType: data.detail.bloodType,
        address: data.detail.address,
        association: data.detail.association,
        village: data.detail.village,
        subdistrict: data.detail.subdistrict,
        religion: data.detail.religion,
        maritalStatus: data.detail.maritalStatus,
        job: data.detail.job,
        citizenship: data.detail.citizenship
      }
      if (data.detail.dateOfBirth !== '') {
        var d = new Date(data.detail.dateOfBirth)
        this.date = d.getDate()
        this.month = parseInt(d.getMonth()) + 1
        this.year = d.getFullYear()
      }
    },
    deleteData () {
      this.load = true
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      var uri = this.$apiUrl + 'patien/' + this.deleteId
      this.$http.delete(uri, config).then(response => {
        this.snackbar = true
        this.text = 'Data berhasil dihapus'
        this.color = 'green'
        this.getPatien()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba Lagi'
        this.color = 'red'
        this.load = false
      })
    },
    UpdateData () {
      this.load = true
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      this.editData.dateOfBirth = this.year + '-' + this.month + '-' + this.date
      uri = this.$apiUrl + 'patien/' + this.editData.id
      this.$http.post(uri, this.editData, config).then(response => {
        console.log(response)
        this.getPatien()
      }).catch(error => {
        console.log(error)
        this.getPatien()
        this.snackbar = true
        this.text = 'try again'
        this.color = 'error'
      })
    }
  }
}
</script>



